.logoBox {
  display: flex;
}

.container {
  width: 100%;
}

.Hello {
  color: #c4c4c4;
  font-size: 50px;
}
.Hello + h1 {
  font-size: 40px;
  color: #c4c4c4;
  margin-bottom: 60px;
}
.Hello ~ h1 ~ p {
  margin: 2px;
  font-size: 30px;
  font-weight: bold;
  font-style: italic;
  color: #c4c4c4;
}
.Hello ~ h1 ~ p span {
  color: aqua;
}
.Hello ~ h1 span {
  color: aqua;
  letter-spacing: 5px;
  margin-left: 10px;
}

.introBox {
  text-align: left;
  margin-left: 100px;
  margin-top: 130px;
}
.RBut {
  display: flex;
  margin-left: 100px;
  align-items: center;
  /* border: 1px solid black; */
  height: auto;
  margin-top: 130px;
  width: 220px;
  justify-content: space-between;
}
.download {
  text-decoration: none;
}
.RBut button {
  display: block;
  width: 160px;
  height: 60px;
  font-size: 30px;
  font-weight: bold;
  border-radius: 12px;
  /* background-color: #343F50; */
  background-color: #003140;
  color: white;
  border: 2px solid white;
  box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset,
    rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset,
    rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px,
    rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px,
    rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
  cursor: pointer;
}

.RBut button:hover {
  color: black;
  /* border-color: black; */
  box-shadow: 0 0 5px #1388a8, 0 0 15px #1388a8, 0 0 20px #1388a8,
    0 0 40px #96d7e9, 0 0 60px #31a9d8, 0 0 10px #96d7e9, 0 0 98px #31a9d8;
  background-color: #fff6a9;
  text-shadow: 0 0 5px #1388a8, 0 0 15px #1388a8, 0 0 20px #1388a8,
    0 0 40px #96d7e9, 0 0 60px #31a9d8, 0 0 10px #96d7e9, 0 0 98px #31a9d8;
  color: #fff6a9;
}

.resume>img{
  width: 50px;
  display: block;
  background-color: #003140;
  color: white;
  cursor: pointer;
  border-radius: 5px;
}

.resume>img:hover{
  box-shadow: 0 0 5px #1388a8, 0 0 15px #1388a8, 0 0 20px #1388a8,
    0 0 40px #96d7e9, 0 0 60px #31a9d8, 0 0 10px #96d7e9, 0 0 98px #31a9d8;
  background-color: #fff6a9;
}

.inContainer {
  display: flex;
}

.imgBox {
  margin-top: 50px;
  margin-left: 150px;
  padding: 50px;
  margin-top: 130px;
}

.imgBox img {
  display: block;
}

